




























import Vue from 'vue';
import { codeMirror } from '@/plugins/codemirror';
import { EditorFromTextArea } from 'codemirror';
import { Splitpanes, Pane } from 'splitpanes';
import json2ts from 'json-to-ts';
import { GraphQlConfiguration, GraphQlState } from '@/store/modules/graphql';
import jsesc from 'jsesc';
import ResultViewerJson from './ResultViewerJson.vue';

export default Vue.extend({
    components: {
        ResultViewerJson,
        Splitpanes,
        Pane,
    },
    data(): {
        editor: EditorFromTextArea | null;
        viewSource: boolean;
        dataJson: string;
    } {
        return {
            editor: null,
            viewSource: false,
            dataJson: '',
        };
    },
    computed: {
        result(): string {
            return this.$store.state.graphql.activeSession.result;
        },
        query(): string {
            return this.$store.state.graphql.activeSession.query;
        },
        template(): string {
            return this.$store.state.graphql.activeSession.template;
        },
        variables(): string {
            return this.$store.state.graphql.activeSession.variables;
        },
        isBusy(): boolean {
            return this.$store.state.graphql.activeSession.isBusy;
        },
        isViewCodeMode(): boolean {
            return this.$store.state.graphql.activeSession.viewCode;
        },
        configuration(): GraphQlConfiguration {
            return (this.$store.state.graphql as GraphQlState).configuration;
        },
    },
    watch: {
        result(newValue): void {
            if (this.editor) {
                this.editor.setValue(newValue);
                this.editor?.setOption('mode', 'graphql-results');
                try {
                    const { data } = JSON.parse(newValue);
                    if (typeof data === 'string') {
                        this.dataJson = data;
                    }
                } catch (err) {
                    this.dataJson = '';
                }
            }
        },
        isViewCodeMode(isViewSource): void {
            if (isViewSource) {
                this.editor?.setOption('mode', 'text/typescript');
                try {
                    if (!this.result) return;
                    const ts = json2ts(JSON.parse(this.result));
                    // eslint-disable-next-line quotes
                    let code = `// insert this part to top`;
                    // eslint-disable-next-line quotes
                    code += `\nimport { fetchGraphQl } from '@/core/fetch-graphql';`;
                    code += '\n\n\n// insert this part to your main function';
                    const options: string[] = [];

                    const jse = (match, capture): string =>
                        `"${jsesc(capture, { quotes: 'double' })
                            .replace(/\n/g, ' ')
                            .replace(/\\n/g, ' ')
                            .replace(/\s\s+/g, ' ')
                            .trim()}"`;
                    const query = this.query.replace(/`([^`]*)`/g, jse);

                    code += `\nconst query = \`${query.trim()}\n\`;`;
                    if (this.variables && this.variables.trim()) {
                        code += `\nconst variables = ${this.variables.replace(/"/g, "'").trim()};`;
                    }
                    if (this.template && this.template.trim()) {
                        const template = this.template.trim();
                        if (template.includes('\n')) {
                            code += `\nconst template = \`${template}\`;`;
                        } else {
                            code += `\nconst template = '${template}';`;
                        }
                        options.push('template');
                    }
                    if (ts[0] !== undefined) {
                        const rootInterface = ts[0];
                        const regEx = /data\s*(.*);/g;
                        const dataInterface = regEx.exec(rootInterface)?.pop();
                        if (!dataInterface) {
                            throw new Error('Could not find data property on result object.');
                        }
                        code += `\nconst result${dataInterface} = await fetchGraphQl(query`;
                        ts.shift();
                    }

                    if (this.configuration.force) options.push('force: true');
                    if (this.configuration.strict) options.push('strict: true');
                    if (this.configuration.serial) options.push('serial: true');

                    if (this.configuration.paging > 0) {
                        options.push(`paging: ${this.configuration.paging}`);
                    } else if (this.configuration.paging < 0) {
                        options.push('paging: false');
                    }
                    if (this.configuration.concurrent > 0) {
                        options.push(`concurrent: ${this.configuration.concurrent}`);
                    }

                    if (options.length > 0) code += `, { ${options.join(', ')} }`;
                    if (this.variables && this.variables.trim()) code += ', variables';
                    code += ');';
                    code += '\n\n\n// insert this part to bottom or top';
                    this.editor?.setValue(
                        `${code}\n${ts.map((it) => `export ${it}`).join('\n\n')}`
                    );
                } catch (err) {
                    this.editor?.setValue(err.message);
                }
            } else {
                this.editor?.setOption('mode', 'graphql-results');
                this.editor?.setValue(this.result);
            }
        },
    },
    mounted() {
        this.editor = codeMirror.fromTextArea(this.$refs.root as HTMLTextAreaElement, {
            lineNumbers: true,
            matchBrackets: true,
            indentUnit: 4,
            lineWrapping: true,
            firstLineNumber: 1,
            theme: 'neo',
            readOnly: true,
            mode: 'graphql-results',
            extraKeys: {
                F11: (cm): void => {
                    cm.setOption('fullScreen', !cm.getOption('fullScreen'));
                },
                Esc: (cm): void => {
                    if (cm.getOption('fullScreen')) cm.setOption('fullScreen', false);
                },
            },
        });
        try {
            const { data } = JSON.parse(this.result);
            if (typeof data === 'string') {
                this.dataJson = data;
            }
        } catch (err) {
            this.dataJson = '';
        }
        this.editor.setValue(this.result);
        this.editor.setSize('100%', '100%');
    },
});
