




















import Vue from 'vue';

export default Vue.extend({
    props: {
        text: {
            type: String,
            required: false,
            default: 'New tab',
        },
        type: {
            type: String,
            required: false,
            default: '',
        },
        active: {
            type: Boolean,
            required: false,
            default: false,
        },
        closeable: {
            type: Boolean,
            required: false,
            default: true,
        },
    },
    methods: {
        handleClick(): void {
            this.$emit('click');
        },
        handleCloseClick(): void {
            this.$emit('click:close');
        },
        handleMiddle(): void {
            this.$emit('click:close');
        },
    },
});
