































































































































import Vue from 'vue';
import { GraphQlConfiguration, GraphQlSession, GraphQlState } from '@/store/modules/graphql';
import Tab from './Tab.vue';

export default Vue.extend({
    components: {
        Tab,
    },
    data() {
        return {
            dialog: false,
        };
    },
    computed: {
        sessions(): GraphQlSession[] {
            return this.$store.state.graphql.sessions;
        },
        activeSession(): GraphQlSession | null {
            return this.$store.state.graphql.activeSession;
        },
        configuration(): GraphQlConfiguration {
            return (this.$store.state.graphql as GraphQlState).configuration;
        },
        force: {
            get(): boolean {
                return this.configuration.force;
            },
            set(_: boolean): void {
                this.$store.commit('graphql/updateConfiguration', { force: _ });
            },
        },
        strict: {
            get(): boolean {
                return this.configuration.strict;
            },
            set(_: boolean): void {
                this.$store.commit('graphql/updateConfiguration', { strict: _ });
            },
        },
        serial: {
            get(): boolean {
                return this.configuration.serial;
            },
            set(_: boolean): void {
                this.$store.commit('graphql/updateConfiguration', { serial: _ });
            },
        },

        paging: {
            get(): number {
                return this.configuration.paging;
            },
            set(_: boolean): void {
                this.$store.commit('graphql/updateConfiguration', { paging: _ });
            },
        },
        concurrent: {
            get(): number {
                return this.configuration.concurrent;
            },
            set(_: boolean): void {
                this.$store.commit('graphql/updateConfiguration', { concurrent: _ });
            },
        },
    },
    methods: {
        tabType(query: string): string {
            if (query.indexOf('mutation') > -1) return 'mutation';
            if (query.indexOf('{') > -1) return 'query';
            return 'No type';
        },
        newTab(): void {
            this.$store.dispatch('graphql/newSession');
        },
        closeTab(sessionId: string): void {
            this.$store.dispatch('graphql/closeSession', sessionId);
        },
        switchTab(sessionId: string): void {
            this.$store.dispatch('graphql/switchSession', sessionId);
        },
        numberFormatter(num: number, type: string): string {
            switch (type) {
                case 'paging':
                    if (num < 0) return 'off';
                    if (num.toString() === '0') return 'auto';
                    break;
                case 'concurrent':
                    if (num <= 0) return 'off';
                    break;
                default:
                    break;
            }
            return num.toString();
        },
    },
});
