






import Vue from 'vue';
import { codeMirror } from '@/plugins/codemirror';
import { EditorFromTextArea } from 'codemirror';
import { getIntrospectionQuery, buildClientSchema, GraphQLSchema } from 'graphql';
import { fetchGraphQl } from '@/core/fetch-graphql';
import { GraphQlConfiguration } from '@/store/modules/graphql';
import { END_POINT } from '@/main';
import getQueryFacts from './utils/getQueryFacts';

export default Vue.extend({
    data(): {
        editor: EditorFromTextArea | null;
        doc: CodeMirror.Doc | null;
        schema: GraphQLSchema | null;
    } {
        return {
            schema: null,
            editor: null,
            doc: new codeMirror.Doc('', 'graphql'),
        };
    },
    computed: {
        query(): string {
            return this.$store.state.graphql.activeSession.query;
        },
        configuration(): GraphQlConfiguration {
            return this.$store.state.graphql.configuration;
        },
        isBusy(): boolean {
            return this.$store.state.graphql.activeSession.isBusy;
        },
    },
    watch: {
        query(newValue): void {
            if (!this.doc) return;
            const matchFields = /{\s*(\b\w+)/g.exec(newValue);
            if (matchFields) {
                this.updateTabbarTitle(matchFields.pop() || 'New tab');
            } else {
                this.updateTabbarTitle('New tab');
            }
            if (newValue !== this.doc.getValue()) {
                this.doc.setValue(newValue);
            }
        },
    },
    methods: {
        updateTabbarTitle(title: string): void {
            this.$store.commit('graphql/updateActiveSessionInfo', { title });
        },
        _updateVariables(): void {
            if (!this.schema || !this.doc) return;
            try {
                const queryFacts = getQueryFacts(this.schema, this.doc.getValue());
                const variableToType = queryFacts?.variableToType;
                if (variableToType) {
                    this.$store.commit('graphql/setVariableToType', JSON.stringify(variableToType));
                } else {
                    this.$store.commit('graphql/setVariableToType', '');
                }
            } catch (err) {
                // eslint-disable-next-line no-empty
            }
        },
        handleChange(): void {
            this.$store.commit('graphql/setQuery', this.doc?.getValue() || '');
            this._updateVariables();
        },
        runQuery(): void {
            if (this.isBusy) {
                this.$store.dispatch('graphql/stopQuery');
            } else {
                this.$store.dispatch('graphql/runQuery');
            }
        },
        prettifyQuery(): void {
            if (!this.editor) return;
            const cursor = this.editor.getCursor();
            this.$store.dispatch('graphql/prettifyQuery');
            this.editor.setCursor(cursor);
        },
        prettifyQueryOnAutoComplete(): void {
            this.$store.dispatch('graphql/prettifyQuery');
        },
        mergeQuery(): void {
            if (!this.editor) return;
            this.$store.dispatch('graphql/mergeQuery');
        },
    },
    async mounted() {
        try {
            const introspectionQuery = getIntrospectionQuery();
            const { useCustomEndpoint, customEndpoint } = this.configuration;
            let url = END_POINT;
            if (useCustomEndpoint) url = customEndpoint;
            try {
                const schemaJson = await fetchGraphQl(introspectionQuery, { url });
                this.schema = buildClientSchema(schemaJson.data);
                this.editor = codeMirror.fromTextArea(this.$refs.root as HTMLTextAreaElement, {
                    lineNumbers: true,
                    matchBrackets: true,
                    dragDrop: true,
                    autoCloseBrackets: true,
                    autofocus: true,
                    keyMap: 'sublime',
                    theme: 'neo',
                    mode: 'graphql',
                    lint: {
                        schema: this.schema,
                    } as any,
                    hintOptions: {
                        schema: this.schema,
                        closeOnUnfocus: false,
                        completeSingle: true,
                        container: this.$refs.wrapper as HTMLTextAreaElement,
                    } as any,
                    gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
                    indentUnit: 4,
                    extraKeys: {
                        'Cmd-Enter': (): void => this.runQuery(),
                        'Ctrl-Enter': (): void => this.runQuery(),
                        'Ctrl-B': (): void => this.prettifyQuery(),
                        'Ctrl-M': (): void => this.mergeQuery(),
                        F11: (cm): void => {
                            cm.setOption('fullScreen', !cm.getOption('fullScreen'));
                        },
                        Esc: (cm): void => {
                            if (cm.getOption('fullScreen')) cm.setOption('fullScreen', false);
                        },
                        'Cmd-Space': (cm): void => {
                            (this.editor as any).showHint({
                                completeSingle: true,
                                container: this.$refs.wrapper as HTMLTextAreaElement,
                            });
                            const completion = cm.state.completionActive.data;
                            codeMirror.on(completion, 'pick', (comp) => {
                                if (comp.description === 'select_all') {
                                    setTimeout(() => this.prettifyQueryOnAutoComplete());
                                }
                            });
                        },
                        'Ctrl-Space': (cm): void => {
                            (this.editor as any).showHint({
                                completeSingle: true,
                                container: this.$refs.wrapper as HTMLTextAreaElement,
                            });
                            const completion = cm.state.completionActive.data;
                            codeMirror.on(completion, 'pick', (comp) => {
                                if (comp.description === 'select_all') {
                                    setTimeout(() => this.prettifyQuery());
                                }
                            });
                        },
                        'Ctrl-F': 'findPersistent',
                        'Ctrl-G': 'findPersistent',
                        'Ctrl-Left': 'goSubwordLeft',
                        'Ctrl-Right': 'goSubwordRight',
                        'Alt-Left': 'goGroupLeft',
                        'Alt-Right': 'goGroupRight',
                    },
                });
            } catch (err) {
                console.log(err);
                this.$store.commit(
                    'graphql/setResult',
                    `Something went wrong :( \n-> ${err.message}`
                );
                return;
            }

            this.editor.setSize('100%', '100%');
            this.editor.setValue(this.query);
            this.doc = this.editor.getDoc();
            this.$store.commit('graphql/setQuery', this.query);
            this.editor.on('change', () => {
                if (this.editor) {
                    this.doc = this.editor.getDoc();
                    this.handleChange();
                }
            });
            let _oldPos = this.editor.getCursor();

            const _onKeyUp = (_cm: CodeMirror.Editor, event: KeyboardEvent): void => {
                if (
                    event.key === 'Enter' &&
                    _oldPos.ch !== _cm.getCursor().ch &&
                    _oldPos.line !== _cm.getCursor().line
                ) {
                    (this.editor as any).showHint({
                        completeSingle: true,
                        container: this.$refs.wrapper as HTMLTextAreaElement,
                    });
                    const completion = _cm.state.completionActive.data;
                    codeMirror.on(completion, 'pick', (comp) => {
                        if (comp.description === 'select_all') {
                            setTimeout(() => this.prettifyQuery());
                        }
                    });
                }
                _oldPos = _cm.getCursor();
            };

            this.editor.on('keyup', _onKeyUp);
        } catch (err) {
            // eslint-disable-next-line no-empty
        }
    },
});
