









import Vue from 'vue';
import { codeMirror } from '@/plugins/codemirror';
import { EditorFromTextArea } from 'codemirror';
import { VariableToType } from './utils/getQueryFacts';

export default Vue.extend({
    data(): {
        editor: EditorFromTextArea | null;
    } {
        return {
            editor: null,
        };
    },
    methods: {
        handleChange(): void {
            this.$store.commit('graphql/setVariables', this.editor?.getValue() || '');
        },
        runQuery(): void {
            this.$store.dispatch('graphql/runQuery');
        },
        prettifyQuery(): void {
            if (!this.editor) return;
            const cursor = { ...this.editor.getCursor() };
            this.$store.dispatch('graphql/prettifyQuery');
            setTimeout(() => this.editor?.setCursor(cursor), 0);
        },
        mergeQuery(): void {
            this.$store.dispatch('graphql/mergeQuery');
        },
    },
    computed: {
        variableToType(): string {
            return this.$store.state.graphql.activeSession.variablesToType;
        },
        variables(): string {
            return this.$store.state.graphql.activeSession.variables;
        },
    },
    watch: {
        variables(newValue): void {
            const cursor = this.editor?.getCursor();
            this.editor?.setValue(newValue);
            if (cursor) this.editor?.setCursor(cursor);
        },
        variableToType(newValue: string): void {
            if (!this.editor) return;
            const variableToType: VariableToType = JSON.parse(newValue);
            this.editor.setOption('lint', {
                variableToType,
            } as any);

            this.editor.setOption('hintOptions', {
                variableToType,
                closeOnUnfocus: false,
                completeSingle: false,
                container: this.$refs.container,
            } as any);
        },
    },
    mounted() {
        this.editor = codeMirror.fromTextArea(
            this.$refs.root as HTMLTextAreaElement,
            {
                lineNumbers: true,
                matchBrackets: true,
                dragDrop: true,
                autoCloseBrackets: true,
                autofocus: true,
                theme: 'neo',
                tabSize: 2,
                mode: 'graphql-variables',
                keyMap: 'sublime',
                showCursorWhenSelecting: true,
                foldGutter: {
                    minFoldSize: 4,
                },
                // lint: {
                //     variableToType: this.variableToType,
                // },
                // hintOptions: {
                //     variableToType: this.variableToType,
                //     closeOnUnfocus: false,
                //     completeSingle: false,
                //     container: this.$refs.container,
                // },
                gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
                extraKeys: {
                    'Ctrl-B': (): void => this.prettifyQuery(),
                    'Ctrl-M': (): void => this.mergeQuery(),
                    'Cmd-Space': (): void =>
                        this.editor?.showHint({
                            completeSingle: false,
                            container: this.$refs.container,
                        } as CodeMirror.ShowHintOptions),
                    'Ctrl-Space': (): void =>
                        this.editor?.showHint({
                            completeSingle: false,
                            container: this.$refs.container,
                        } as CodeMirror.ShowHintOptions),
                    'Cmd-Enter': (): void => this.runQuery(),
                    'Ctrl-Enter': (): void => this.runQuery(),
                },
            } as any
        );
        this.editor.setSize('100%', '100%');
        this.editor.on('change', () => {
            if (this.editor) {
                this.handleChange();
            }
        });
        this.editor.setValue(this.variables);
        let _oldPos = this.editor.getCursor();

        const _onKeyUp = (_cm: CodeMirror.Editor, event: KeyboardEvent): void => {
            if (
                event.key === 'Enter' &&
                _oldPos.ch !== _cm.getCursor().ch &&
                _oldPos.line !== _cm.getCursor().line
            ) {
                (this.editor as any).showHint({
                    completeSingle: true,
                    container: this.$refs.wrapper as HTMLTextAreaElement,
                });
                _cm.showHint({
                    completeSingle: false,
                    container: this.$refs.container,
                } as CodeMirror.ShowHintOptions);
            }
            _oldPos = _cm.getCursor();
        };

        this.editor.on('keyup', _onKeyUp);
    },
});
