




import Vue from 'vue';
import AppTile from '@/components/layout/Tile.vue';

export default Vue.extend({
    methods: {
        runQuery(): void {
            if (!this.isBusy) {
                this.$store.dispatch('graphql/runQuery');
            } else {
                this.$store.dispatch('graphql/stopQuery');
            }
        },
    },
    components: {
        AppTile,
    },
    computed: {
        isBusy(): boolean {
            return this.$store.state.graphql.activeSession.isBusy;
        },
        color(): string {
            return this.isBusy ? 'red' : 'blue';
        },
        icon(): string {
            return this.isBusy ? 'mdi-pause' : 'mdi-play';
        },
    },
});
