






import Vue from 'vue';
import { codeMirror } from '@/plugins/codemirror';
import { EditorFromTextArea } from 'codemirror';

export default Vue.extend({
    data(): {
        editor: EditorFromTextArea | null;
    } {
        return {
            editor: null,
        };
    },
    props: {
        data: {
            type: String,
            required: true,
            default: '',
        },
    },
    watch: {
        data(newValue): void {
            this.editor?.setValue(newValue);
        },
    },
    mounted() {
        this.editor = codeMirror.fromTextArea(this.$refs.root as HTMLTextAreaElement, {
            lineNumbers: true,
            matchBrackets: true,
            indentUnit: 4,
            lineWrapping: true,
            firstLineNumber: 1,
            theme: 'neo',
            readOnly: true,
            mode: 'javascript',
            gutters: ['CodeMirror-lint-markers'],
            lint: true,
            extraKeys: {
                F11: (cm): void => {
                    cm.setOption('fullScreen', !cm.getOption('fullScreen'));
                },
                Esc: (cm): void => {
                    if (cm.getOption('fullScreen')) cm.setOption('fullScreen', false);
                },
            },
        });

        this.editor.setValue(this.data);
        this.editor.setSize('100%', '100%');
    },
});
