
























































import Vue from 'vue';
import AppTile from '@/components/layout/Tile.vue';
import { GraphQlSession } from '@/store/modules/graphql';
import ExecuteButton from './ExecuteButton.vue';

export default Vue.extend({
    components: {
        AppTile,
        ExecuteButton,
    },
    computed: {
        activeSession(): GraphQlSession | null {
            return this.$store.state.graphql.activeSession;
        },
    },
    methods: {
        prettifyQuery(): void {
            this.$store.dispatch('graphql/prettifyQuery');
        },
        mergeQuery(): void {
            this.$store.dispatch('graphql/mergeQuery');
        },
        viewCode(): void {
            this.$store.commit('graphql/updateActiveSessionInfo', {
                viewCode: !this.activeSession?.viewCode,
            });
        },
    },
});
