
































import Vue from 'vue';
import WindowFrame from '@/components/layout/WindowFrame.vue';
import { Splitpanes, Pane } from 'splitpanes';
import { GraphQlState, PanelSize } from '@/store/modules/graphql';
import { END_POINT } from '@/main';
import QueryEditor from './GraphQl/QueryEditor.vue';
import TemplateEditor from './GraphQl/TemplateEditor.vue';
import ResultViewer from './GraphQl/ResultViewer.vue';
import VariableEditor from './GraphQl/VariableEditor.vue';
import ToolBar from './GraphQl/ToolBar.vue';
import TabBar from './GraphQl/TabBar.vue';

export default Vue.extend({
    computed: {
        splitPanelRootSizes(): PanelSize[] {
            return (this.$store.state.graphql as GraphQlState).configuration.splitPanelRootSizes;
        },
        splitPanelChildSizes(): PanelSize[] {
            return (this.$store.state.graphql as GraphQlState).configuration.splitPanelChildSizes;
        },
        endpoint(): string {
            const { useCustomEndpoint, customEndpoint } = (this.$store.state
                .graphql as GraphQlState).configuration;
            return useCustomEndpoint ? customEndpoint : END_POINT;
        },
    },
    components: {
        WindowFrame,
        QueryEditor,
        ResultViewer,
        TemplateEditor,
        VariableEditor,
        ToolBar,
        TabBar,
        Splitpanes,
        Pane,
    },
    methods: {
        handleResizeRoot(sizes): void {
            this.$store.commit('graphql/updateConfiguration', { splitPanelRootSizes: sizes });
        },
        handleResizeChild(sizes): void {
            this.$store.commit('graphql/updateConfiguration', { splitPanelChildSizes: sizes });
        },
    },
});
